import React from 'react';
import { Link } from 'react-router-dom';

let LoginForm = () => {

    return (
        <React.Fragment>
                <div className="container mt-5 d-flex justify-content-center ">
                    <div className="row">
                        <div className="col">
                            <div className="card ">
                                <div className="card-header text-white bg-warning text-center">
                                    <h2>Login Form</h2>
                                </div>
                                <div className="card-body">
                                    <form>
                                        <div className="form-group">
                                            <label>Username</label>
                                            <input className="form-control" type="text" placeholder="Username" />
                                        </div>
                                        <div className="form-group">
                                            <label> Password</label>
                                            <input className="form-control" type="password" placeholder="password" />
                                        </div>
                                        <p><Link className="nav-link text-danger" to='/ForgetPassword'>Forget Password</Link></p>
                                        <button className="btn btn-success">Submit</button>
                                        <Link to='/RegistrationForm'><button className="btn btn-primary"> New Registration</button></Link>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        </React.Fragment>
    )
}

export default LoginForm; 