import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Axios from 'axios';

let RegistrationForm = () => {

    let [userData, setUser] = useState(
        {
            name: "",
            rollno: 0,
            mobile: 0,
            address: ""
        }
    );

    let update = (event) => {
        setUser({
            ...userData,
            [event.target.name]: event.target.value
        })
    }

    let submit = (event) => {
        Axios.post(
            "http://127.0.0.1:5004/api/users", userData)
            .then((response) => {
                console.log(response)
            })
            .catch(
                (error) => { console.log("error") }
            );

        // event.preventDefault();
        console.log(userData);

    }

    return (
        <React.Fragment>
            <div className="container d-flex justify-content-center mt-3">
                <div className="row">
                    <div className="col-md-12 ">
                        <div className="card">
                            <div className="card-header text-white bg-info text-center">
                                <h2>Registration Form</h2>
                            </div>
                            <div className="card-body">
                                <form onSubmit={submit}>
                                    <div className="form-group">
                                        <label>Name</label>
                                        <input className="form-control" type="text" placeholder="Username" name="name" onChange={update} required />
                                    </div>
                                    <div className="form-group">
                                        <label>Roll No.</label>
                                        <input className="form-control" type="number" placeholder="Roll No." required name="rollno" onChange={update} />
                                    </div>
                                    <div className="form-group">
                                        <label>Mobile Number</label>
                                        <input className="form-control" type="text" placeholder="Mobile Number" name="mobile" onChange={update} required />
                                    </div>
                                    <div className="form-group">
                                        <label>Address</label>
                                        <textarea className="form-control" type="text" placeholder="Address" name="address" onChange={update} required></textarea>
                                    </div>
                                    {/* <div className="form-group">
                                            <label>Password</label>
                                            <input className="form-control" type="password" placeholder="Enter password" name="password" onChange={update} required />
                                        </div> */}
                                    {/* <Link className="text-white btn" to='/LoginForm'> <button className="btn btn-primary" >Register</button></Link> */}
                                    <button type="submit" className="btn btn-primary" >Register
                                        {
                                            !userData.name == "" &&
                                            <Link to='/LoginForm'></Link>
                                        }
                                    </button>

                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default RegistrationForm;