import React from 'react';
import RangeSlider from 'react-bootstrap-range-slider';

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 25
    }
  }

  updateRange = (event) => {

  }

  render() {
    return (
      <React.Fragment>
        <header className="bg-info text-white text-center py-3" >
          <h3>User-Registration</h3>
        </header>
        <div className="container mt-4">
          <div className="row">
            <div className="col-md-4">
              <h2>Image</h2>
            </div>
            <div className="col-md-8">
              <form>
                <div className="form-group">
                  <div className="row">

                    <div className="col-auto">
                      <label for="name" className="col-form-label">Name</label>
                    </div>
                    <div className="col-auto">
                      <input id="name" type="text" className="form-control ml-3" placeholder="First Name" />
                    </div>
                    <div className="col-auto">
                      <input type="text" className="form-control ml-3" placeholder="Last Name" />
                    </div>
                  </div>

                  <div className="row mt-3">
                    <div className="col-auto">
                      <label className="col-form-label" for="customRange3">Age</label>
                    </div>
                    <div className="col-auto ml-5">
                        <input type="range" 
                                style={{ width: "300%" }} width="100%" min="1" max="100" step="1" id="customRange3" 
                                value={this.state.value}
                                onChange={e => this.setState({ value: e.target.value })}
                                />
                        <p>{this.state.value}</p>
                        <div id="pmd-slider-tooltip"  className="pmd-range-slider pmd-range-tooltip"></div>
                      {/* <RangeSlider
                        
                        style={{ width: "500%" }}
                        min="1" max="100"
                        value={this.state.value}
                        onChange={e => this.setState({ value: e.target.value })}
                      /> */}
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </React.Fragment>
    )
  };

}

export default Login;


